.main-container {
  width: 250px;
  height: 234px;
  font-family: Segoe UI, Tahoma, sans-serif;
  font-size: 75%;
}

header {
  text-align: left;
  font-size: 115%;
  font-weight: bold;
}

input {
  width: 70%;
  height: 30px;
  cursor: pointer;
  border: none;
  border-bottom: 1px solid #26619c;
  outline: 0;
  margin: auto auto 20px;
  display: block;
}

input:focus, input:active {
  content: "";
  border: none;
  border-bottom: 3px solid #26619c;
  outline: 0;
  margin-bottom: 18px;
  transition: all .3s;
}

#autoGroupBtn {
  color: #fff;
  width: 80%;
  text-align: center;
  height: 40px;
  cursor: pointer;
  background-color: #26619c;
  border: none;
  margin: auto auto 20px;
  display: block;
}

#onLoading {
  display: none;
}

.spinner {
  margin-top: 93px;
  margin-left: 93px;
  animation: 1.8s linear infinite rotator;
}

@keyframes rotator {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(270deg);
  }
}

#path {
  stroke-dasharray: 100;
  stroke-dashoffset: 0;
  transform-origin: center;
  margin: auto;
  animation: 1.8s ease-in-out infinite dash, 7.2s ease-in-out infinite colors;
}

@keyframes colors {
  0% {
    stroke: #4285f4;
  }

  25% {
    stroke: #de3e35;
  }

  50% {
    stroke: #f7c223;
  }

  75% {
    stroke: #1b9a59;
  }

  100% {
    stroke: #4285f4;
  }
}

@keyframes dash {
  0% {
    stroke-dashoffset: 100px;
  }

  50% {
    stroke-dashoffset: 25px;
    transform: rotate(135deg);
  }

  100% {
    stroke-dashoffset: 100px;
    transform: rotate(450deg);
  }
}

